.forest-entry {
    background: #070707b9;
    font-family: Lato;
    text-align: center;
    justify-content: center;
  }
  .forest-flex-row {
    display: flex;
    flex-direction: row;
  }
  
  .forest-flex-column {
    display: flex;
    flex-direction: column;
  }

  .forest-button {
    background-color: #D9BA8C;
    color: rgb(36, 34, 34);
    font-weight: bold;
  }

  .forest-message {
    color: #bc9864;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
  }

  .tab {
    display: inline-block;
    margin-left: 40px;
  }

  .forest-p {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    background-image: linear-gradient(to bottom, rgb(59, 59, 58), #14110bd0);
    color: aliceblue;
    margin: 0 2rem;
  }

  .forest-p p {
    padding: 0.5rem 0;
  }